html, body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden; /* Prevent scrolling */
}

/* main-container ensures everything is positioned and stacked vertically */
.main-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  background-color: white;
}

/* Container for positioning */
.triangle-container {
  position: relative;
  width: 100vw; /* Full width */
  height: 40vh; /* Reduced height to make it smaller */
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

/* Triangle shape */
.triangle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw; /* Adjust width to desired size */
  height: 150px; /* Reduced height to make it smaller */
  overflow: hidden;
}

@keyframes waveAnimation {
  0% {
    d: path('M 0,100 Q 20,75 45,85 T 100,15 L 100,0 L 0,0 Z'); /* Starting position */
  }
  50% {
    d: path('M -50,100 Q 20,60 50,85 T 150,25 L 150,0 L -50,0 Z'); /* More movement at bottom-left */
  }
  100% {
    d: path('M 0,100 Q 20,75 45,85 T 100,15 L 100,0 L 0,0 Z'); /* Reset to start */
  }
}

.wave {
  animation: waveAnimation 8s ease-in-out infinite;
}

/* Chat box container */
.chat-container {
  width: 90%;
  max-width: 600px;
  background: #ffffff;
  padding: 5px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 170vh; /* Adjusted height */
  max-height: 500px; /* Adjusted max-height */
  margin-top: -30px; /* Moved chat box container up */
  position: relative;
}

.input-container {
  margin-top: 0; /* Adjust or remove as needed */
  margin-bottom: 5px;
  padding-top: 5px; /* Adjust if padding exists */
}

.btn-invis {
  position: absolute;
  top: 20px;
  left: 5px;
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
  z-index: 2;
}

.invis {
  font-size: 2rem;
}

.icon-send {
  display: inline-block; /* Ensure the element can be transformed */
  transform: rotate(-45deg); /* Rotate the icon by 45 degrees (you can change this value) */
}

/* Coco styles */
.coco-small {
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50px; /* Adjust as needed */
  left: 200px; /* Adjust as needed */
  background-color: black;
  width: 100px; /* Reduced size to make it smaller */
  height: 100px; /* Reduced size to make it smaller */
  border-radius: 50%;
  gap: 20px;
  outline: 20px solid white;
}

.eye {
  width: 30px; /* Reduced size to make it smaller */
  height: 20px; /* Reduced size to make it smaller */
  margin-bottom: 5px;
  background-color: #5A9BFF;
  border-radius: 50%;
  animation: blink 3s infinite;
  clip-path: ellipse(100% 50% at 50% 50%);
}

@keyframes blink {
  0%, 100% {
    transform: scaleY(1);
    clip-path: ellipse(100% 50% at 50% 50%); /* Normal eye shape */
  }
  50%, 60% {
    transform: scaleY(0.8);
    /* Create crescent shape: cut the bottom half to create a crescent */
    clip-path: ellipse(100% 35% at 50% 35%);
  }
}